<template lang="pug">
    footer
        div(class="container")
            div(class="footer-content")
                .footer-logo 
                    img(:src="FooterData.logo", alt="")
                .footer-contact 
                    .row 
                        .col-lg-4.box
                            .info-box
                                .icon 
                                    fa(class="theicon" :icon="['fas', 'headset']")
                                .info-details
                                    h3 {{ $t("footer-help") }}
                                    a(dir="ltr" :href="'tel:' + FooterData.phone") {{ FooterData.phone }}
                        .col-lg-4.box
                            .info-box
                                .icon 
                                    fa(class="theicon" :icon="['far', 'envelope']")
                                .info-details
                                    h3 {{ $t("footer-email") }}
                                    a(:href="'mailto:' + FooterData.email") {{ FooterData.email }}
                        .col-lg-4.box
                            .info-box
                                .icon 
                                    fa(class="theicon" :icon="['fas', 'map-marker-alt']")
                                .info-details
                                    h3 {{ $t("footer-address") }}
                                    p {{ FooterData.address }}
            .copyright 
                .links 
                    ul 
                        li 
                            router-link(to="/" exact) {{ $t("footer-home") }}
                        li
                            router-link(to="/about") {{ $t("footer-about") }}
                        li
                            router-link(to="/services") {{ $t("footer-services") }}
                        li
                            router-link(to="/solutions") {{ $t("footer-solutions") }}
                        li
                            router-link(to="/portfolio") {{ $t("footer-portfolio") }}
                        li
                            router-link(to="/blogs") {{ $t("footer-blogs") }}
                        li
                            router-link(to="/plans") {{ $t("footer-plans") }}
                        li
                            router-link(to="/careers") {{ $t("footer-careers") }}
                        li
                            router-link(to="/offers") {{ $t("footer-offers") }}
                        li
                            router-link(to="/contact") {{ $t("footer-contact") }}
                .copy-text
                    p {{ $t("copyrights") }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Footer",
    mounted() {
        this.$store.dispatch("getFooterData");
    },
    computed: {
        ...mapState(["FooterData"]),
    },
};
</script>
