<template lang="pug">
    header
        section.top-nav
            .container
                .topnav-content
                    .row 
                        div(class="col-6 col-sm-6 col-md-5 col-lg-3 box")
                            .part
                                .icon
                                    fa(class="theicon" :icon="['fas', 'headset']")
                                .text
                                    span.d-none.d-md-inline {{ $t("nav-help") }}:
                                    a(dir="ltr" :href="'tel:' + HeaderData.phone") {{ HeaderData.phone }}
                        div(class="col-4 col-sm-4 col-md-4 box d-none d-lg-block")
                            .part
                                .icon
                                    fa(class="theicon" :icon="['far', 'envelope']")
                                .text 
                                    span {{ $t("nav-email") }}:
                                    a(:href="'mailto:' + HeaderData.email") {{ HeaderData.email }}
                        div(class="col-4 col-sm-4 col-md-5 col-lg-4 box")
                            .part.social-media
                                .icon.d-none.d-md-inline
                                    fa(class="theicon" :icon="['far', 'thumbs-up']")
                                .text.d-none.d-md-inline
                                    span {{ $t("nav-social") }}:
                                ul 
                                    li 
                                        a(:href="HeaderData.facebook", target="_blank")
                                            fa(class="theicon" :icon="['fab', 'facebook-f']")
                                    li 
                                        a(:href="HeaderData.twitter", target="_blank")
                                            fa(class="theicon" :icon="['fab', 'twitter']")
                                    li 
                                        a(:href="HeaderData.linkedIn", target="_blank")
                                            fa(class="theicon" :icon="['fab', 'linkedin-in']")
                                    li 
                                        a(:href="HeaderData.instagram", target="_blank")
                                            fa(class="theicon" :icon="['fab', 'instagram']")
                        div(class="col-2 col-sm-2 col-md-2 col-lg-1 box")
                            .part
                                .language 
                                    //- router-link(to="/" @click="changeLang" title="English" value="en") EN 
                                    //- span |
                                    //- router-link(to="/" @click="changeLang" title="Arabic" value="ar") AR
                                    select(@change="handleChangeLang($event)" v-model="lang")
                                        option(value="en") EN
                                        option(value="ar") AR
        nav(class='navbar navbar-expand-lg navbar-light')
            div(class='container')
                router-link(to="/" class='navbar-brand')
                    img(:src="HeaderData.logo", class="img-fluid", alt="")
                button(class='navbar-toggler' type='button' @click="toggleActive" :class="{'close-btn' : isClose}")
                    span
                div(class='collapse navbar-collapse' :class="{'active' : isActive}")
                    ul(class='navbar-nav')
                        li(class='nav-item')
                            router-link(to="/" exact class='nav-link') {{ $t("header-home") }}
                        li(class='nav-item')
                            router-link(to="/about" class='nav-link') {{ $t("header-about") }}
                        li(class='nav-item')
                            router-link(to="/services" class='nav-link') {{ $t("header-services") }}
                        li(class='nav-item')
                            router-link(to="/solutions" class='nav-link') {{ $t("header-solutions") }}
                        li(class='nav-item')
                            router-link(to="/portfolio" class='nav-link') {{ $t("header-portfolio") }}
                        li(class='nav-item')
                            router-link(to="/blogs" class='nav-link') {{ $t("header-blogs") }}
                        li(class='nav-item')
                            router-link(to="/plans" class='nav-link') {{ $t("header-plans") }}
                        li(class='nav-item')
                            router-link(to="/contact" class='nav-link') {{ $t("header-contact") }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Header",
    data: () => ({
        isActive: false,
        isClose: false,
        lang: window.location.pathname.replace(/^\/([^/]+).*/i, "$1") || "en",
    }),
    methods: {
        toggleActive: function () {
            this.isActive = !this.isActive;
            this.isClose = !this.isClose;
        },
        handleChangeLang(event) {
            // event.preventDefault();
            var sub = window.location.pathname.substring(3);
            var url =
                window.location.protocol +
                "//" +
                window.location.host +
                "/" +
                event.target.value +
                sub;

            window.location = url;
        },
    },
    mounted() {
        this.$store.dispatch("getHeaderData");
    },
    computed: {
        ...mapState(["HeaderData"]),
    },
    created() {
        let htmlElement = document.querySelector("html");
        if (this.lang == "ar") {
            htmlElement.setAttribute("dir", "rtl");
            htmlElement.setAttribute("lang", "ar");
        }
        let urlLang = `${process.env.BASE_URL.substring(3)}${window.location.pathname
    .replace(/^\/([^/]+).*/i, "$1")
    }`;
        console.log(urlLang);
    },
};
</script>
